.block {
  padding-bottom: 20px;
  margin-bottom: 20px;
}

.block .sub-nav-group span.field--name-title,
.block h2,
.sub-nav-group .block span.field--name-title {
  padding-bottom: 0.2em;
  letter-spacing: -1px;
  margin-bottom: 15px;
}

.block-page-title-block .sub-nav-group span.field--name-title,
.block-page-title-block h1,
.block-page-title-block h2,
.layoutmanager .sub-nav-group span.field--name-title,
.layoutmanager h1,
.layoutmanager h2,
.sub-nav-group .block-page-title-block span.field--name-title,
.sub-nav-group .layoutmanager span.field--name-title {
  line-height: 115%;
  padding-bottom: 30px;
  background-image: url(../img/h1-underline.png);
  background-repeat: no-repeat;
  background-position: left bottom;
  font-weight: 300;
  font-family: Merriweather, Cambria, serif;
  letter-spacing: -1px;
  margin: 0 !important;
}

.abstract {
  font-size: 1.6em;
  padding-bottom: 20px;
  border-bottom: 1px solid #e3e3e3;
  font-weight: 200;
  line-height: 1.4em;
  margin-bottom: 20px;
}

@media (max-width: 767px) {
  .abstract {
    font-size: 22px;
    line-height: 1.45;
  }
}

.mq--t:after {
  display: block;
  clear: both;
  content: " ";
}

a[href$=".pdf"]:after,
a[href$=".zip"]:after,
a[href$=".doc"]:after,
a[href$=".docx"]:after,
a[href$=".xls"]:after,
a[href$=".xlsx"]:after,
a[href$=".ppt"]:after,
a[href$=".pptx"]:after {
  content: "";
  display: inline-block;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  transform: translate(0, 0);
  width: 20px;
  height: 20px;
  text-align: right;
  margin-left: 8px;
  position: relative;
  top: 2px;
}

a[href$=".pdf"]:after {
  background: url(../img/pdf.svg) 0 100% no-repeat;
  background-size: contain;
}

#banner--treasury-alert {
  text-align: center;
  color: #fff;
  z-index: 1;
  line-height: 16px;
  font-weight: 600;
  font-family: "Source Sans Pro", Calibri, sans-serif;
  font-size: 12px;
  padding: 6px 10px 8px;
}

#banner--treasury-alert.green {
  background-color: #4b8025;
  color: #fff;
}

#banner--treasury-alert a:visited {
  color: #fff;
}

#banner--treasury-alert a:hover,
#banner--treasury-alert a:focus,
#banner--treasury-alert a:active {
  color: inherit;
  font-weight: inherit;
  text-decoration: underline;
}

@media (min-width: 1025px) {
  #banner--treasury-alert {
    padding: 6px 30px 8px;
    font-size: 16px;
  }

  #banner--treasury-alert a {
    font-size: 16px;
  }
}

@media (min-width: 768px) and (max-width: 1024px) {
  #banner--treasury-alert {
    margin-top: 68px;
    font-size: 14px;
    padding: 6px 30px 8px;
  }

  #banner--treasury-alert a {
    font-size: 14px;
  }
}

