// Program index styles
// ---------------------------------------------------------------------------
.era-program {
  &__toc {
    list-style-type: none;
    padding-left: 0;
  }

  &__section {
    margin: 60px 0 24px;
  }

  &__find {
    margin-top: 1.5em;
  }

  &__territory-url,
  &__tribe-url,
  &__state-url {
    margin: 8px 0;
    padding: 0;
  }

  &__name,
  &__admin,
  &__territory,
  &__tribe,
  &__state-url,
  &__territory-url,
  &__tribe-url,
  &__open-data {
    font-family: "Source Sans Pro", Calibri, sans-serif;
  }

  &__territory,
  &__tribe {
    padding-bottom: 4px;
    color: color($theme-color-primary-dark);
  }

  &__name,
  &__admin,
  &__territory,
  &__tribe {
    text-transform: capitalize;
  }

  &__state-url,
  &__territory-url,
  &__tribe-url,
  &__admin,
  &__list,
  &__status {
    font-size: 1.1em;
    line-height: 160%;
  }

  &__state-url,
  &__territory-url,
  &__tribe-url {
    margin-top: 0;
  }

  &__territory-url,
  &__tribe-url {
    font-weight: normal;
  }

  &__state-url {
    font-weight: bold;
  }

  &__state--v1 {
    margin-top: 40px;
  }

  &__open-data {
    margin-top: 48px;
  }

  &__list {
    margin-bottom: 16px;
  }

  &__url {
    margin: 0;
  }

  &__app-status {
    font-size: 2em;
    font-weight: bold;
  }

  &__app-status--accepting {
    color: color($theme-color-success-dark);
  }

  &__app-status--will-be-accepting {
    color: color($theme-color-warning-dark);
  }

  &__app-status--not-accepting {
    color: color($theme-color-error-dark);
  }
}

.era-guidance__hero {
  margin: units(4) 0;
}

// card/hero/base-lightest
.tre-card {
  &__container {
    position: relative;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    height: 100%;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    margin-left: 0.5rem;
    margin-right: 0.5rem;
    font-family: Source Sans Pro Web, Helvetica Neue, Helvetica, Roboto, Arial,
      sans-serif;
    font-size: 1.06rem;
    line-height: 1.5;
  }

  &__header {
    padding: 1.5rem 1.5rem 0.5rem;
  }

  &__body {
    flex: 1 1 0%;
    padding: 0.5rem 1.5rem;
    flex-basis: auto;
  }

  &__footer {
    padding: 0.5rem 1.5rem 1.5rem;
  }
}

.tre-card-hero {
  &__header {
    padding-bottom: 1rem;
  }
}

.tre-card.tre-card-hero {
  // Have to override some style leaking in from layoutmanager h2
  .tre-card-hero__heading {
    font-size: 40px;
    line-height: 115%;
    margin: 0 !important;
    padding-bottom: 30px;
    background-image: none;
    font-weight: 300;
    font-family: Merriweather, Cambria, serif;
  }
}

.tre-card-hero--base-lightest {
  &__container {
    color: color($theme-color-base-ink);
    background-color: color($theme-color-base-lightest);
  }
}
